import React from 'react';
import Navbar from './components/Navbar';
import {BrowserRouter as Router, Switch, Route, Redirect} from 'react-router-dom';
import './App.css';
import Home from './components/pages/Home';
import About from './components/pages/About';
import Galery from './components/pages/Galery';
import Projects from './components/pages/Projects';
import Contact from './components/pages/Contact';
import SignUp from './components/pages/SignUp';

function App() {
  return (
    <>
      <Router>
        <Navbar />
        <Switch>
          <Redirect exact from='/' to='/cskstudios/' />

          <Route path='/cskstudios/' exact component={Home} />
          <Route path='/cskstudios/about' component={About} />
          <Route path='/cskstudios/galery' component={Galery} />
          <Route path='/cskstudios/projects' component={Projects} />
          <Route path='/cskstudios/contact' component={Contact} />
          <Route path='/cskstudios/sign-up' component={SignUp} />
        </Switch>
      </Router>
    </>
  );
}

export default App;
